<template>
  <b-container>
    <b-card class="my-2">
      <b-form @submit="submitInfo">
        <b-form-group
          id="input-group-1"
          label="Email address:"
          label-for="input-1"
          description="We'll never share your email with anyone else."
        >
          <b-input-group>
            <b-input-group-prepend is-text>
              <b-icon icon="envelope-fill"></b-icon>
            </b-input-group-prepend>
            <b-form-input
              id="input-1"
              v-model="localUser.email"
              type="email"
              placeholder="Enter email"
              trim
              @keypress="clip($event, localUser.email, maxEmailLength)"
            ></b-form-input>
          </b-input-group>
          <small v-if="Boolean(localUser.email) && localUser.email.length > maxEmailLength" class="invalid">Email should be under {{maxEmailLength}} characters</small>
        </b-form-group>

        <b-form-group id="input-group-2" label="Your Name:" label-for="input-2">
          <b-input-group>
            <b-input-group-prepend is-text>
              <b-icon icon="person-fill"></b-icon>
            </b-input-group-prepend>
            <b-form-input
              id="input-2"
              v-model="localUser.name"
              placeholder="Enter name"
              trim
              @keypress="clip($event, localUser.name, maxNameLength)"
            ></b-form-input>
          </b-input-group>
          <small v-if="Boolean(localUser.name) && localUser.name.length > maxNameLength" class="invalid">Name should be under {{maxNameLength}} characters</small>
        </b-form-group>

        <b-form-group id="input-group-3" label="District:" label-for="input-3">
          <b-form-select
            id="input-3"
            v-model="localUser.district"
            :options="districts"
            required
          ></b-form-select>
        </b-form-group>

        <b-form-group id="input-group-4" label="Postal Code:" label-for="input-4">
          <b-input-group>
            <b-input-group-prepend is-text>
              <b-icon icon="geo-alt"></b-icon>
            </b-input-group-prepend>
            <b-form-input
              id="input-4"
              v-model="localUser.postalCode"
              placeholder="Enter Postal Code"
              type="number"
              :max="9999"
              :min="1000"
              trim
              @keypress="clip($event, localUser.postalCode, maxPostalCodeLength)"
            ></b-form-input>
          </b-input-group>
        </b-form-group>

        <b-form-group id="input-group-5" label="Your Default Address:" label-for="input-5">
          <b-input-group>
            <b-input-group-prepend is-text>
              <b-icon icon="geo-alt"></b-icon>
            </b-input-group-prepend>
            <b-form-input
              id="input-5"
              v-model="localUser.address"
              placeholder="Enter Default Address"
              trim
              @keypress="clip($event, localUser.address, maxAddressLength)"
            ></b-form-input>
          </b-input-group>
          <small v-if="Boolean(localUser.address) && localUser.address.length > maxAddressLength" class="invalid">Address should be under {{maxAddressLength}} characters</small>
        </b-form-group>

<!--        <b-form-group id="input-group-3" label="Food:" label-for="input-3">-->
<!--          <b-form-select-->
<!--            id="input-3"-->
<!--            v-model="localUser.food"-->
<!--            :options="foods"-->
<!--          ></b-form-select>-->
<!--        </b-form-group>-->

<!--        <b-form-group id="input-group-4">-->
<!--          <b-form-checkbox-group v-model="localUser.checked" id="checkboxes-4">-->
<!--            <b-form-checkbox value="me">Check me out</b-form-checkbox>-->
<!--            <b-form-checkbox value="that">Check that out</b-form-checkbox>-->
<!--          </b-form-checkbox-group>-->
<!--        </b-form-group>-->

        <b-overlay :show="updateBusy" opacity="0.4" spinner-small
                   spinner-variant="primary" class="d-inline block mx-3">
          <b-button type="submit" variant="primary" :disabled="updateDisabled">Update</b-button>
        </b-overlay>
        <b-button variant="danger" @click="resetInfo">Reset</b-button>
      </b-form>
<!--      <b-card class="mt-3" header="Form Data Result">-->
<!--        <pre class="m-0">{{ localUser }}</pre>-->
<!--      </b-card>-->
    </b-card>

    <b-card class="my-2">
      <b-card-title>
        <b-button @click="showPasswordForm = !showPasswordForm" variant="warning">Change Password</b-button>
      </b-card-title>
      <b-form v-if="showPasswordForm" @submit="submitPassword">
        <b-form-group id="input-group-6" label="Old Password" label-for="input-6">
          <b-input-group>
            <b-form-input
              id="input-6"
              :type="selectIcon?'password':'text'"
              v-model="oldPassword"
              placeholder="Enter Old Password"
              required
              @keypress="clip($event, oldPassword, maxPasswordLength)"
            ></b-form-input>
            <b-input-group-append is-text>
              <b-icon :icon="selectIcon?'eye-slash-fill':'eye-fill'" @click="selectIcon=!selectIcon"></b-icon>
            </b-input-group-append>
          </b-input-group>
          <small v-if="Boolean(oldPassword) && oldPassword.length > maxPasswordLength" class="invalid">Password should be under {{maxPasswordLength}} characters</small>
        </b-form-group>

        <b-form-group id="input-group-7" label="New Password" label-for="input-7">
          <b-input-group>
            <b-form-input
              id="input-7"
              :type="selectIcon1?'password':'text'"
              v-model="newPassword"
              placeholder="Enter New Password"
              required
              @keypress="clip($event, newPassword, maxPasswordLength)"
            ></b-form-input>
            <b-input-group-append is-text>
              <b-icon :icon="selectIcon1?'eye-slash-fill':'eye-fill'" @click="selectIcon1=!selectIcon1"></b-icon>
            </b-input-group-append>
          </b-input-group>
          <small v-if="Boolean(newPassword) && newPassword.length > maxPasswordLength" class="invalid">Password should be under {{maxPasswordLength}} characters</small>
        </b-form-group>
        <b-overlay :show="changeBusy" opacity="0.4" spinner-small
                   spinner-variant="primary" class="d-inline block mx-3">
          <b-button type="submit" variant="primary" :disabled="changeDisabled">Change</b-button>
        </b-overlay>
        <b-button type="reset" variant="danger">Reset</b-button>
      </b-form>
    </b-card>

  </b-container>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import inputClipper from '../../../mixins/inputClipper'

export default {
  name: 'UserProfileEdit',
  title: 'Edit My Profile',
  data () {
    return {
      selectIcon: true,
      selectIcon1: true,
      localUser: null,
      oldPassword: null,
      newPassword: null,
      showPasswordForm: false,
      updateBusy: false,
      changeBusy: false,
      maxNameLength: 100,
      maxEmailLength: 100,
      maxPostalCodeLength: 4,
      maxAddressLength: 500,
      maxPasswordLength: 100
    }
  },
  computed: {
    ...mapGetters([
      'user',
      'districts'
    ]),
    updateDisabled () {
      return (this.updateBusy ||
        (Boolean(this.localUser.email) && this.localUser.email.length > this.maxEmailLength) ||
        (Boolean(this.localUser.name) && this.localUser.name.length > this.maxNameLength) ||
        (Boolean(this.localUser.address) && this.localUser.address.length > this.maxAddressLength)
      )
    },
    changeDisabled () {
      return (this.changeBusy ||
        (Boolean(this.oldPassword) && this.oldPassword.length > this.maxPasswordLength) ||
        (Boolean(this.newPassword) && this.newPassword.length > this.maxPasswordLength)
      )
    }
  },
  mixins: [
    inputClipper
  ],
  methods: {
    ...mapActions([
      'changeInfo',
      'changePassword'
    ]),
    submitInfo (event) {
      event.preventDefault()
      this.updateBusy = true
      this.changeInfo(this.localUser)
        .then(response => {
          this.updateBusy = false
          // console.log(response)
        })
        .catch(() => {
          this.updateBusy = false
          // console.log(error.response)
        })
    },
    resetInfo () {
      this.localUser = JSON.parse(JSON.stringify(this.user))
    },
    submitPassword (event) {
      event.preventDefault()
      this.changeBusy = true
      this.changePassword({
        phoneNo: this.user.phoneNo,
        oldPassword: this.oldPassword,
        newPassword: this.newPassword
      })
        .then(response => {
          // console.log(response)
          this.showPasswordForm = false
          this.changeBusy = false
        })
        .catch(() => {
          this.changeBusy = false
          // console.log(error.response)
        })
    }
  },
  created () {
    this.resetInfo()
  }
}
</script>

<style scoped>
.invalid {
  float: right;
  color: red;
}
</style>
